@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* custom body settings */
  background: #E5E5E5;
  font-family: 'Merriweather', serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



/*Обнуление*/
*{
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}
*,*:before,*:after{
  box-sizing: border-box;
}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}

nav,footer,header,aside{display: block;}

html,body{
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
input,button,textarea{font-family:inherit;}

input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}

h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: 400;}
/*--------------------*/

*{color:#343030}.container{max-width:1168px;margin:0 auto}._temp{text-align:center}._tempBtn{padding:15px 30px;margin-top:50px;margin-right:20px}h1{font-size:36px}h2{font-size:27px}h3{font-size:24px}h4,h5,h6{font-size:21px}h1,h3,h4{font-weight:600}h2,h5{font-weight:400}h6{font-weight:300}a{text-decoration:none}a:hover{color:#725d5b}button{text-decoration:none;outline:none;border:none;background:#403432;color:#F4F6F9;transition:background 0.5s ease}button i{color:#F4F6F9;margin-right:7px}button:hover{background:#776763}button:disabled{background:#C1B4B1}


