}

.header{border-bottom:1px solid #cdcdcd;margin-bottom:45px}.header .container{height:97px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.header__search{display:-webkit-flex;display:flex}.header__search-input{border:1px solid #cdcdcd;padding:14px;width:294px}.header__search-button{padding:14px 36px}

.navigation{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.navigation__img{width:48px;height:48px}.navigation__menu a{margin-left:48px;font-size:14px}

.paintings__header{margin-bottom:39px}.paintings__content{display:-webkit-flex;display:flex}

.paintCard{border:1px solid #cdcdcd;width:280px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-right:32px}.paintCard.isSold{opacity:0.4}.paintCard__sold{-webkit-align-self:flex-end;align-self:flex-end;margin-top:34px;font-weight:700}.paintCard__image img{width:280px;height:160px}.paintCard__content .container{padding:25px}.paintCard__content-title,.paintCard__content-author{font-size:18px}.paintCard__content-author{margin-bottom:23px}.paintCard__content__priceBlock{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.paintCard__content__priceBlock-startPrice{color:#A0A0A0;-webkit-text-decoration-line:line-through;text-decoration-line:line-through;font-size:14px}.paintCard__content__priceBlock-price{font-size:16px}.paintCard__content__priceBlock-price,.paintCard__content__priceBlock-startPrice{line-height:150%}.paintCard__content__priceBlock button{padding:14px 30px}.paintCard__content__priceBlock button.inBasket{padding:14px 15px;background:#5B3A32}

.footer{background:#ECEAEA}.footer .container{height:96px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.footer__contacts{display:-webkit-flex;display:flex}.footer__contacts-contact{margin-left:51px}.footer__contacts-contact i{margin-right:10px}

.content{min-height:calc(100vh - 97px - 96px - 45px)}

.contact{max-width:800px;margin:0 auto;display:-webkit-flex;display:flex}.contact__image{margin-right:30px}.contact__image img{width:150px;height:150px;border-radius:10px}.contact__description{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around}.contact__description h6{font-size:15px;line-height:30px}.contact__description-body{line-height:150%}.contact__description-buttons button{padding:10px 30px}

